






































import { Component, Prop, Vue } from "vue-property-decorator";
import { getQuery } from "@/util/cookie";
import {
  setRefundTaobaoKey,
  getRefundInfo,
  RefundInfo
} from "@/api/taskrefund";
import { getCreateTask } from "@/api/task";

@Component({
  components: {}
})
export default class TaskFree extends Vue {
  private tid:string = "";
  private keyword:string='€RORXbceOGYJ€'
  private initData: RefundInfo = {
    key_word: "",
    task_order_id: "",
    status: "",
    task_no: "",
    gift: "",
    url: "",
    square_url: ""
  };
  private key = "";
  private created() {
    getCreateTask("refund", getQuery("tid"))
      .then((res: any) => {
        //数据逻辑处理
        this.initData = res;
      })
      .catch((err: { message: string }) => {
        this.$toast(err.message);
      });
  }
  private onCopy() {
    this.$toast("复制成功");
  }
  private commitTaoBaoKey() {
    setRefundTaobaoKey(this.initData.task_order_id, this.key)
      .then((res: {}) => {
        this.$toast("淘口令提交成功");
        //数据逻辑处理
        setTimeout(() => {
          window.location.href = "/tasklist?type=refund";
        }, 3000);
      })
      .catch((err: { message: string }) => {
        this.$toast(err.message);
      });
  }
}



